'use client';

import { useState } from 'react';
import { Users, UserPlus, Loader2 } from 'lucide-react';
import clsx from 'clsx';

const workloadTone = (value) => {
  if (value < 60) return 'bg-emerald-100 text-emerald-700';
  if (value < 90) return 'bg-amber-100 text-amber-700';
  return 'bg-rose-100 text-rose-600';
};

const workloadBar = (value) => {
  if (value < 60) return 'bg-emerald-500';
  if (value < 90) return 'bg-amber-500';
  return 'bg-rose-500';
};

const initials = (name = '') => {
  const [first = '', second = ''] = name.split(' ');
  return `${first.charAt(0)}${second.charAt(0)}`.toUpperCase() || name.charAt(0).toUpperCase();
};

export default function TeamOverview({ teamMembers = [], onCreate = async () => {} }) {
  const [form, setForm] = useState({ name: '', role: 'Architect', capacity: 5 });
  const [submitting, setSubmitting] = useState(false);

  const handleSubmit = async (event) => {
    event.preventDefault();
    if (!form.name.trim()) return;
    try {
      setSubmitting(true);
      await onCreate(form);
      setForm({ name: '', role: 'Architect', capacity: 5 });
    } finally {
      setSubmitting(false);
    }
  };

  return (
    <section className="surface-card space-y-5">
      <header className="flex flex-wrap items-start justify-between gap-4">
        <div>
          <p className="text-xs font-semibold uppercase tracking-[0.35em] text-slate-400">Teams</p>
          <h2 className="mt-2 text-2xl font-semibold text-slate-900">Team workload</h2>
          <p className="text-sm text-slate-500">
            An at-a-glance overview of your team capacity and assigned tasks.
          </p>
        </div>
        <div className="rounded-2xl border border-slate-200 px-4 py-2 text-right">
          <p className="text-[10px] uppercase tracking-[0.35em] text-slate-400">Members</p>
          <p className="text-2xl font-semibold text-slate-900">{teamMembers.length}</p>
        </div>
      </header>

      <form
        onSubmit={handleSubmit}
        className="grid gap-3 rounded-2xl border border-slate-200 bg-slate-50/60 p-4 sm:grid-cols-2"
      >
        <label className="flex flex-col gap-1 text-sm text-slate-600">
          Full name
          <input
            placeholder="Alex Chen"
            value={form.name}
            onChange={(event) => setForm((prev) => ({ ...prev, name: event.target.value }))}
          />
        </label>
        <label className="flex flex-col gap-1 text-sm text-slate-600">
          Role
          <input
            placeholder="Lead Architect"
            value={form.role}
            onChange={(event) => setForm((prev) => ({ ...prev, role: event.target.value }))}
          />
        </label>
        <label className="flex flex-col gap-1 text-sm text-slate-600">
          Weekly capacity
          <input
            type="number"
            min={1}
            max={10}
            value={String(form.capacity)}
            onChange={(event) =>
              setForm((prev) => ({
                ...prev,
                capacity: Math.max(1, Number(event.target.value) || 1),
              }))
            }
          />
        </label>
        <div className="flex items-end justify-end">
          <button type="submit" disabled={submitting} className="btn-primary">
            {submitting ? <Loader2 size={16} className="animate-spin" /> : <UserPlus size={16} />}
            {submitting ? 'Adding' : 'Add member'}
          </button>
        </div>
      </form>

      <div className="grid gap-4 md:grid-cols-2">
        {teamMembers.length ? (
          teamMembers.map((member) => {
            const tasks = Array.isArray(member.tasks) ? member.tasks : [];
            const capacity = member.capacity || 5;
            const derivedCount = tasks.length || member.taskCount || 0;
            const workload = capacity ? Math.min(100, Math.round((derivedCount / capacity) * 100)) : 0;

            return (
              <article
                key={member._id}
                className="flex items-center gap-4 rounded-2xl border border-slate-200 bg-white p-4"
              >
                <div className="flex h-14 w-14 items-center justify-center rounded-2xl bg-slate-100 text-lg font-semibold text-slate-700">
                  {initials(member.name)}
                </div>
                <div className="flex-1">
                  <div className="flex items-center justify-between gap-2">
                    <div>
                      <p className="font-semibold text-slate-900">{member.name}</p>
                      <p className="text-sm text-slate-500">{member.role}</p>
                    </div>
                    <span className={clsx('status-pill text-xs', workloadTone(workload))}>
                      {workload}% load
                    </span>
                  </div>
                  <div className="mt-2 h-2 rounded-full bg-slate-100">
                    <div
                      className={clsx('h-full rounded-full', workloadBar(workload))}
                      style={{ width: `${Math.min(workload, 100)}%` }}
                    />
                  </div>
                  <p className="mt-1 text-xs text-slate-500">
                    {derivedCount} / {capacity} tasks
                  </p>
                </div>
              </article>
            );
          })
        ) : (
          <div className="rounded-2xl border border-dashed border-slate-300 bg-white px-4 py-8 text-center text-slate-500">
            Add your core team to start tracking workload.
          </div>
        )}
      </div>
    </section>
  );
}
