'use client';

import { useEffect, useMemo, useState } from 'react';
import { motion } from 'framer-motion';
import { Building2, Plus, LayoutDashboard, CheckCircle2, ListChecks, CalendarClock } from 'lucide-react';
import ProjectDashboard from '../components/ProjectDashboard';
import TaskPanel from '../components/TaskPanel';
import TeamOverview from '../components/TeamOverview';
import StudioInsights from '../components/StudioInsights';
import { api } from '../lib/api';

const normalizeProject = (project) => ({
  ...project,
  taskSummary: project.taskSummary ?? { total: 0, completed: 0 },
});

export default function HomePage() {
  const [projects, setProjects] = useState([]);
  const [teamMembers, setTeamMembers] = useState([]);
  const [selectedProjectId, setSelectedProjectId] = useState(null);
  const [loading, setLoading] = useState(true);
  const [projectSyncing, setProjectSyncing] = useState(() => new Set());

  useEffect(() => {
    let cancelled = false;

    const bootstrap = async () => {
      setLoading(true);
      try {
        const [projectsResponse, teamResponse] = await Promise.all([
          api.getProjects(),
          api.getTeamMembers(),
        ]);
        if (!cancelled) {
          setProjects(projectsResponse.map(normalizeProject));
          setTeamMembers(teamResponse);
          if (projectsResponse.length) {
            setSelectedProjectId(projectsResponse[0]._id);
          }
        }
      } catch (error) {
        console.error('Failed to load workspace data', error);
      } finally {
        if (!cancelled) {
          setLoading(false);
        }
      }
    };

    bootstrap();
    return () => {
      cancelled = true;
    };
  }, []);

  const selectedProject = useMemo(() => {
    if (!projects.length) {
      return null;
    }
    return projects.find((project) => project._id === selectedProjectId) ?? projects[0];
  }, [projects, selectedProjectId]);

  const studioSnapshot = useMemo(() => {
    const totalProjects = projects.length;
    const completed = projects.filter((project) => project.status === 'Completed').length;
    const active = totalProjects - completed;
    const tasks = projects.reduce((acc, project) => acc + (project.taskSummary?.total || 0), 0);
    const now = Date.now();
    const dueSoonBoundary = now + 1000 * 60 * 60 * 24 * 7;
    const dueSoon = projects.filter((project) => {
      if (!project?.dueDate || project.status === 'Completed') return false;
      const time = new Date(project.dueDate).getTime();
      return time >= now && time <= dueSoonBoundary;
    }).length;
    const studioCount = new Set(projects.map((project) => project.studio).filter(Boolean)).size;

    return {
      totalProjects,
      active,
      completed,
      tasks,
      dueSoon,
      studios: studioCount,
    };
  }, [projects]);

  const heroStats = useMemo(
    () => [
      {
        label: 'Active briefs',
        value: studioSnapshot.active,
        caption: 'Currently in delivery',
        icon: LayoutDashboard,
        gradient: 'from-sky-500/40 via-sky-500/5 to-transparent',
        accent: 'text-sky-200',
      },
      {
        label: 'Completed',
        value: studioSnapshot.completed,
        caption: 'Shipped this quarter',
        icon: CheckCircle2,
        gradient: 'from-emerald-500/30 via-emerald-500/5 to-transparent',
        accent: 'text-emerald-200',
      },
      {
        label: 'Tasks scheduled',
        value: studioSnapshot.tasks,
        caption: 'Across all phases',
        icon: ListChecks,
        gradient: 'from-amber-500/30 via-amber-500/5 to-transparent',
        accent: 'text-amber-200',
      },
      {
        label: 'Due this week',
        value: studioSnapshot.dueSoon,
        caption: 'Need follow-up',
        icon: CalendarClock,
        gradient: 'from-rose-500/40 via-rose-500/5 to-transparent',
        accent: 'text-rose-200',
      },
    ],
    [studioSnapshot],
  );

  const scrollToProjectForm = () => {
    const element = document.getElementById('project-create');
    if (element) {
      element.scrollIntoView({ behavior: 'smooth', block: 'start' });
    }
  };

  const handleProjectCreate = async (payload) => {
    const project = await api.createProject(payload);
    const normalized = normalizeProject(project);
    setProjects((prev) => [normalized, ...prev]);
    setSelectedProjectId(normalized._id);
  };

  const handleProjectDelete = async (projectId) => {
    const project = projects.find((item) => item._id === projectId);
    if (!project) return;
    if (!window.confirm(`Delete ${project.name}? Tasks for this project will also be removed.`)) {
      return;
    }
    await api.deleteProject(projectId);
    const wasSelected = selectedProjectId === projectId;
    setProjects((prev) => {
      const next = prev.filter((item) => item._id !== projectId);
      if (wasSelected) {
        setSelectedProjectId(next[0]?._id ?? null);
      }
      return next;
    });
  };

  const handleProgressUpdate = async (projectId, progressValue) => {
    setProjectSyncing((prev) => {
      const next = new Set(prev);
      next.add(projectId);
      return next;
    });
    try {
      const updated = await api.updateProjectProgress(projectId, progressValue);
      setProjects((prev) =>
        prev.map((project) =>
          project._id === projectId ? normalizeProject({ ...project, ...updated }) : project,
        ),
      );
    } finally {
      setProjectSyncing((prev) => {
        const next = new Set(prev);
        next.delete(projectId);
        return next;
      });
    }
  };

  const handleProjectHydrate = (updatedProject) => {
    if (!updatedProject) return;
    setProjects((prev) =>
      prev.map((project) => (project._id === updatedProject._id ? normalizeProject(updatedProject) : project)),
    );
  };

  const refreshTeam = async () => {
    const list = await api.getTeamMembers();
    setTeamMembers(list);
  };

  const handleTeamCreate = async (payload) => {
    await api.createTeamMember(payload);
    await refreshTeam();
  };

  if (loading) {
    return (
      <main className="flex min-h-screen items-center justify-center text-slate-400">
        <div className="space-y-2 text-center">
          <p className="text-sm uppercase tracking-[0.3em]">Loading studio</p>
          <div className="h-1 w-40 overflow-hidden rounded-full bg-white/10">
            <div className="h-full w-1/2 animate-pulse rounded-full bg-sky-400" />
          </div>
        </div>
      </main>
    );
  }

  return (
    <main className="min-h-screen pb-16 pt-10">
      <div className="mx-auto max-w-7xl space-y-10 px-4 lg:px-6">
        <motion.nav
          initial={{ opacity: 0, y: -12 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.35 }}
          className="glass-panel flex flex-wrap items-center justify-between gap-4 rounded-[28px] px-6 py-4 shadow-glass"
        >
          <div className="flex items-center gap-3">
            <div className="rounded-2xl bg-white/10 p-3 text-sky-300">
              <Building2 size={20} />
            </div>
            <div>
              <p className="text-xs uppercase tracking-[0.35em] text-slate-400">Workspace</p>
              <div className="flex items-center gap-2 text-sm text-slate-400">
                <span className="text-base font-semibold text-white">VZNX studio control</span>
                <span className="rounded-full border border-white/15 px-3 py-0.5 text-[11px] uppercase tracking-[0.3em]">
                  Live
                </span>
              </div>
            </div>
          </div>
          <div className="flex flex-wrap items-center gap-2">
            <button type="button" className="btn-ghost text-sm">
              Invite client
            </button>
            <button type="button" onClick={scrollToProjectForm} className="btn-primary text-sm">
              <Plus size={16} aria-hidden />
              New project
            </button>
          </div>
        </motion.nav>

        <motion.section
          initial={{ opacity: 0, y: 16 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.4 }}
          className="glass-panel relative overflow-hidden rounded-[36px] border border-white/10 p-8 shadow-glass"
        >
          <div className="absolute inset-0 -z-10 bg-grid bg-[size:30px_30px] opacity-30" />
          <div className="absolute -right-20 top-0 h-72 w-72 rounded-full bg-sky-500/15 blur-[120px]" />
          <div className="flex flex-col gap-8 md:flex-row md:items-start md:justify-between">
            <div className="flex-1">
              <p className="text-xs uppercase tracking-[0.4em] text-slate-400">Mission control</p>
              <h2 className="mt-3 text-3xl font-semibold text-white sm:text-[2.6rem] sm:leading-tight">
                A professional cockpit for architecture delivery teams
              </h2>
              <p className="mt-4 max-w-2xl text-base text-slate-300">
                Track briefs, unblock owners, and keep studios aligned without bouncing between spreadsheets
                and chat threads. Your entire delivery rhythm, reflected in one canvas.
              </p>
              <div className="mt-6 flex flex-wrap gap-4 text-xs uppercase tracking-[0.35em] text-slate-500">
                <span>Real-time sync</span>
                <span>Program-level visibility</span>
                <span>Client-ready views</span>
              </div>
            </div>
            <div className="w-full max-w-sm rounded-[28px] border border-white/10 bg-white/5 px-6 py-5">
              <p className="text-sm uppercase tracking-[0.35em] text-slate-500">Team</p>
              <p className="text-4xl font-semibold text-white">{teamMembers.length}</p>
              <p className="text-xs text-slate-400">Active collaborators</p>
              <div className="mt-4 border-t border-white/10 pt-4">
                <p className="text-sm uppercase tracking-[0.35em] text-slate-500">Studios</p>
                <p className="text-3xl font-semibold text-white">
                  {studioSnapshot.studios || (studioSnapshot.totalProjects ? 1 : 0)}
                </p>
                <p className="text-xs text-slate-400">Working inside VZNX</p>
              </div>
            </div>
          </div>
          <div className="mt-8 grid gap-4 sm:grid-cols-2 xl:grid-cols-4">
            {heroStats.map((stat) => {
              const Icon = stat.icon;
              return (
                <article key={stat.label} className="stat-card group border-white/10 bg-white/5">
                  <div
                    className={`absolute inset-0 opacity-90 blur-xl transition duration-300 group-hover:opacity-100 bg-gradient-to-br ${stat.gradient}`}
                  />
                  <div className="relative flex items-start justify-between gap-4">
                    <div>
                      <p className="text-[11px] uppercase tracking-[0.4em] text-slate-500">{stat.label}</p>
                      <p className={`mt-3 text-3xl font-semibold ${stat.accent}`}>{stat.value ?? 0}</p>
                      <p className="text-xs text-slate-400">{stat.caption}</p>
                    </div>
                    <div className="rounded-2xl border border-white/20 bg-white/10 p-2 text-white">
                      <Icon size={20} aria-hidden />
                    </div>
                  </div>
                </article>
              );
            })}
          </div>
        </motion.section>

        <section className="grid gap-6 xl:grid-cols-[1.15fr_0.85fr]">
          <ProjectDashboard
            projects={projects}
            selectedProjectId={selectedProjectId}
            onSelect={setSelectedProjectId}
            onCreate={handleProjectCreate}
            onDelete={handleProjectDelete}
            onProgressUpdate={handleProgressUpdate}
            busyProjectIds={projectSyncing}
          />
          <TaskPanel
            project={selectedProject}
            teamMembers={teamMembers}
            onProjectUpdate={handleProjectHydrate}
            onTeamRefresh={refreshTeam}
          />
        </section>

        <section className="grid gap-6 lg:grid-cols-[1fr_0.7fr]">
          <TeamOverview teamMembers={teamMembers} onCreate={handleTeamCreate} />
          <StudioInsights projects={projects} teamMembers={teamMembers} />
        </section>
      </div>
    </main>
  );
}
