'use client';

import { useEffect, useMemo, useState } from 'react';
import { BadgeCheck, Loader2, Plus, Trash2 } from 'lucide-react';
import clsx from 'clsx';
import { api } from '../lib/api';

const priorityTone = {
  High: 'text-rose-600 bg-rose-50 border border-rose-100',
  Medium: 'text-amber-600 bg-amber-50 border border-amber-100',
  Low: 'text-emerald-600 bg-emerald-50 border border-emerald-100',
};

export default function TaskPanel({
  project,
  teamMembers = [],
  onProjectUpdate,
  onTeamRefresh = () => {},
}) {
  const [tasks, setTasks] = useState([]);
  const [loading, setLoading] = useState(false);
  const [form, setForm] = useState({ name: '', assigneeId: '', priority: 'Medium' });
  const [submitting, setSubmitting] = useState(false);
  const [busyTaskId, setBusyTaskId] = useState(null);

  useEffect(() => {
    let ignore = false;
    const load = async () => {
      if (!project?._id) {
        setTasks([]);
        return;
      }
      setLoading(true);
      try {
        const data = await api.getTasks(project._id);
        if (!ignore) {
          setTasks(data);
        }
      } catch (error) {
        console.error('Failed to load tasks', error);
      } finally {
        if (!ignore) {
          setLoading(false);
        }
      }
    };

    load();
    return () => {
      ignore = true;
    };
  }, [project?._id]);

  const summary = useMemo(() => {
    const completed = tasks.filter((task) => task.status === 'Complete').length;
    return {
      completed,
      total: tasks.length,
    };
  }, [tasks]);

  const pushProjectUpdate = (updatedProject, nextTasks) => {
    if (!updatedProject || !onProjectUpdate) return;
    const completed = nextTasks.filter((task) => task.status === 'Complete').length;
    onProjectUpdate({
      ...updatedProject,
      taskSummary: {
        total: nextTasks.length,
        completed,
      },
    });
  };

  const handleSubmit = async (event) => {
    event.preventDefault();
    if (!project?._id || !form.name.trim()) return;
    try {
      setSubmitting(true);
      const payload = {
        name: form.name,
        projectId: project._id,
        assigneeId: form.assigneeId || undefined,
        priority: form.priority,
      };
      const { task, project: updatedProject } = await api.createTask(payload);
      setTasks((prev) => {
        const next = [task, ...prev];
        pushProjectUpdate(updatedProject, next);
        return next;
      });
      onTeamRefresh();
      setForm({ name: '', assigneeId: '', priority: 'Medium' });
    } catch (error) {
      console.error('Failed to create task', error);
    } finally {
      setSubmitting(false);
    }
  };

  const toggleStatus = async (task) => {
    try {
      setBusyTaskId(task._id);
      const nextStatus = task.status === 'Complete' ? 'Incomplete' : 'Complete';
      const { task: updatedTask, project: updatedProject } = await api.toggleTaskStatus(
        task._id,
        nextStatus,
      );
      setTasks((prev) => {
        const next = prev.map((item) => (item._id === task._id ? updatedTask : item));
        pushProjectUpdate(updatedProject, next);
        return next;
      });
      onTeamRefresh();
    } catch (error) {
      console.error('Failed to toggle task', error);
    } finally {
      setBusyTaskId(null);
    }
  };

  const removeTask = async (task) => {
    try {
      setBusyTaskId(task._id);
      const { project: updatedProject } = await api.deleteTask(task._id);
      setTasks((prev) => {
        const next = prev.filter((item) => item._id !== task._id);
        pushProjectUpdate(updatedProject, next);
        return next;
      });
      onTeamRefresh();
    } catch (error) {
      console.error('Failed to delete task', error);
    } finally {
      setBusyTaskId(null);
    }
  };

  return (
    <section className="surface-card space-y-5">
      <header className="flex items-start justify-between gap-3">
        <div>
          <p className="text-xs font-semibold uppercase tracking-[0.35em] text-slate-400">
            Tasks
          </p>
          <h2 className="mt-2 text-2xl font-semibold text-slate-900">
            {project ? project.name : 'Select a project'}
          </h2>
          <p className="text-sm text-slate-500">
            {project
              ? `Client deliverables due ${project.dueDate ? new Date(project.dueDate).toLocaleDateString() : 'TBD'}.`
              : 'Pick a project from the list to see and manage its tasks.'}
          </p>
        </div>
        <div className="rounded-2xl border border-slate-200 px-4 py-2 text-right">
          <p className="text-[10px] uppercase tracking-[0.35em] text-slate-400">Complete</p>
          <p className="text-2xl font-semibold text-slate-900">
            {summary.completed}/{summary.total}
          </p>
        </div>
      </header>

      <form
        onSubmit={handleSubmit}
        className="grid gap-3 rounded-2xl border border-slate-200 bg-slate-50/60 p-4"
      >
        <div className="flex flex-col gap-1 text-sm text-slate-600">
          Task name
          <input
            placeholder="Draft initial blueprints"
            value={form.name}
            onChange={(event) => setForm((prev) => ({ ...prev, name: event.target.value }))}
            disabled={!project}
          />
        </div>
        <div className="grid gap-3 sm:grid-cols-2">
          <label className="flex flex-col gap-1 text-sm text-slate-600">
            Priority
            <select
              value={form.priority}
              onChange={(event) => setForm((prev) => ({ ...prev, priority: event.target.value }))}
              disabled={!project}
            >
              {['High', 'Medium', 'Low'].map((level) => (
                <option key={level} value={level}>
                  {level}
                </option>
              ))}
            </select>
          </label>
          <label className="flex flex-col gap-1 text-sm text-slate-600">
            Assignee
            <select
              value={form.assigneeId}
              onChange={(event) => setForm((prev) => ({ ...prev, assigneeId: event.target.value }))}
              disabled={!project}
            >
              <option value="">Unassigned</option>
              {teamMembers.map((member) => (
                <option key={member._id} value={member._id}>
                  {member.name}
                </option>
              ))}
            </select>
          </label>
        </div>
        <div className="flex justify-end">
          <button type="submit" className="btn-primary" disabled={submitting || !project}>
            {submitting ? <Loader2 size={16} className="animate-spin" /> : <Plus size={16} />}
            {submitting ? 'Adding' : 'Add task'}
          </button>
        </div>
      </form>

      <div className="space-y-3">
        {loading ? (
          <div className="flex h-32 items-center justify-center rounded-2xl border border-slate-200 bg-white text-slate-400">
            <Loader2 className="animate-spin" />
          </div>
        ) : tasks.length ? (
          tasks.map((task) => (
            <article
              key={task._id}
              className="flex flex-wrap items-center justify-between gap-3 rounded-2xl border border-slate-200 bg-white px-4 py-3"
            >
              <div className="flex-1">
                <div className="flex items-center gap-2">
                  <button
                    type="button"
                    onClick={() => toggleStatus(task)}
                    className={clsx(
                      'rounded-full border p-1',
                      task.status === 'Complete'
                        ? 'border-emerald-400 bg-emerald-50 text-emerald-600'
                        : 'border-slate-200 text-slate-400',
                    )}
                  >
                    {busyTaskId === task._id ? (
                      <Loader2 size={16} className="animate-spin" />
                    ) : (
                      <BadgeCheck size={16} />
                    )}
                  </button>
                  <div>
                    <p
                      className={clsx(
                        'font-semibold',
                        task.status === 'Complete' ? 'text-slate-400 line-through' : 'text-slate-900',
                      )}
                    >
                      {task.name}
                    </p>
                    <div className="mt-1 flex flex-wrap items-center gap-2 text-xs text-slate-500">
                      <span className={clsx('status-pill border px-3 py-1', priorityTone[task.priority])}>
                        {task.priority} priority
                      </span>
                      <span>{task.assignee?.name || 'Unassigned'}</span>
                    </div>
                  </div>
                </div>
              </div>
              <button
                type="button"
                onClick={() => removeTask(task)}
                className="rounded-full border border-slate-200 p-2 text-slate-400 hover:border-rose-200 hover:text-rose-500"
                disabled={busyTaskId === task._id}
              >
                <Trash2 size={16} />
              </button>
            </article>
          ))
        ) : (
          <div className="rounded-2xl border border-dashed border-slate-300 bg-white px-4 py-8 text-center text-slate-500">
            {project ? 'No tasks for this project yet.' : 'Select a project to see its tasks.'}
          </div>
        )}
      </div>
    </section>
  );
}
