'use client';

import { useMemo, useState } from 'react';
import { Plus, Search, Filter, Trash2 } from 'lucide-react';
import clsx from 'clsx';

const statusTone = {
  Planned: 'bg-slate-100 text-slate-600',
  'In Progress': 'bg-blue-100 text-blue-700',
  'At Risk': 'bg-amber-100 text-amber-700',
  Completed: 'bg-emerald-100 text-emerald-700',
  Cancelled: 'bg-rose-100 text-rose-600',
};

const formatDate = (value) => {
  if (!value) return 'No deadline';
  return new Intl.DateTimeFormat('en', { month: 'short', day: 'numeric' }).format(new Date(value));
};

export default function ProjectDashboard({
  projects = [],
  selectedProjectId,
  onSelect = () => {},
  onCreate = async () => {},
  onDelete = () => {},
  onProgressUpdate = async () => {},
  busyProjectIds = new Set(),
}) {
  const [form, setForm] = useState({
    name: '',
    studio: 'Core Studio',
    dueDate: '',
    color: '#1d4ed8',
  });
  const [showForm, setShowForm] = useState(false);
  const [progressDrafts, setProgressDrafts] = useState({});
  const [submitting, setSubmitting] = useState(false);
  const [search, setSearch] = useState('');

  const filtered = useMemo(() => {
    if (!search.trim()) return projects;
    return projects.filter((project) =>
      project.name?.toLowerCase().includes(search.trim().toLowerCase()),
    );
  }, [projects, search]);

  const totals = useMemo(() => {
    if (!projects.length) {
      return { average: 0, active: 0, completed: 0 };
    }
    const completed = projects.filter((p) => p.status === 'Completed').length;
    const active = projects.length - completed;
    const average = Math.round(
      projects.reduce((acc, project) => acc + (project.progress || 0), 0) / projects.length,
    );

    return { average, active, completed };
  }, [projects]);

  const handleSubmit = async (event) => {
    event.preventDefault();
    if (!form.name.trim()) return;
    try {
      setSubmitting(true);
      await onCreate(form);
      setForm({
        name: '',
        studio: 'Core Studio',
        dueDate: '',
        color: '#1d4ed8',
      });
      setShowForm(false);
    } finally {
      setSubmitting(false);
    }
  };

  const handleProgressChange = (projectId, value) => {
    setProgressDrafts((prev) => ({
      ...prev,
      [projectId]: value,
    }));
  };

  const commitProgress = async (projectId) => {
    const value = progressDrafts[projectId];
    if (value === undefined) return;
    try {
      await onProgressUpdate(projectId, value);
    } finally {
      setProgressDrafts((prev) => {
        const copy = { ...prev };
        delete copy[projectId];
        return copy;
      });
    }
  };

  return (
    <section className="surface-card space-y-6">
      <header className="flex flex-wrap items-start justify-between gap-4">
        <div>
          <p className="text-xs font-semibold uppercase tracking-[0.35em] text-slate-400">
            Projects
          </p>
          <h2 className="mt-2 text-3xl font-semibold text-slate-900">Manage ongoing briefs</h2>
          <p className="text-sm text-slate-500">
            Track each client engagement, progress, and handover dates in one place.
          </p>
        </div>
        <div className="flex flex-wrap items-center gap-3">
          <div className="rounded-2xl border border-slate-200 px-4 py-2 text-right">
            <p className="text-[10px] uppercase tracking-[0.35em] text-slate-400">Active</p>
            <p className="text-2xl font-semibold text-slate-900">{totals.active}</p>
          </div>
          <div className="rounded-2xl border border-slate-200 px-4 py-2 text-right">
            <p className="text-[10px] uppercase tracking-[0.35em] text-slate-400">Avg. Progress</p>
            <p className="text-2xl font-semibold text-slate-900">{totals.average}%</p>
          </div>
          <button
            type="button"
            className="btn-primary text-sm"
            onClick={() => setShowForm((prev) => !prev)}
          >
            <Plus size={16} />
            {showForm ? 'Close' : 'Add project'}
          </button>
        </div>
      </header>

      <div className="flex flex-wrap gap-3">
        <div className="relative flex-1 min-w-[220px]">
          <Search size={16} className="absolute left-3 top-1/2 -translate-y-1/2 text-slate-400" />
          <input
            className="w-full border-slate-200 pl-10"
            placeholder="Search projects"
            value={search}
            onChange={(event) => setSearch(event.target.value)}
          />
        </div>
        <button type="button" className="btn-secondary text-sm">
          <Filter size={16} />
          Filter
        </button>
      </div>

      <div className="overflow-hidden rounded-2xl border border-slate-200 bg-white">
        <table className="table-card text-sm">
          <thead>
            <tr>
              <th>Project name</th>
              <th>Status</th>
              <th>Progress</th>
              <th>Tasks</th>
              <th>Due</th>
              <th>Actions</th>
            </tr>
          </thead>
          <tbody>
            {filtered.length ? (
              filtered.map((project) => {
                const isSelected = project._id === selectedProjectId;
                const rowProgress = progressDrafts[project._id] ?? project.progress ?? 0;
                const busy = busyProjectIds?.has?.(project._id) ?? false;

                return (
                  <tr
                    key={project._id}
                    className={clsx(
                      'cursor-pointer transition hover:bg-slate-50',
                      isSelected && 'bg-slate-50',
                    )}
                    onClick={() => onSelect(project._id)}
                  >
                    <td>
                      <div className="font-semibold text-slate-900">{project.name}</div>
                      <p className="text-xs text-slate-500">{project.studio || 'Studio TBD'}</p>
                    </td>
                    <td>
                      <span
                        className={clsx(
                          'status-pill',
                          statusTone[project.status] || 'bg-slate-100 text-slate-600',
                        )}
                      >
                        {project.status}
                      </span>
                    </td>
                    <td>
                      <div className="flex items-center gap-3">
                        <div className="h-2 flex-1 rounded-full bg-slate-100">
                          <div
                            className="h-full rounded-full bg-blue-500"
                            style={{ width: `${rowProgress}%` }}
                          />
                        </div>
                        <span className="w-12 text-right text-xs text-slate-500">{rowProgress}%</span>
                      </div>
                      <input
                        type="range"
                        min={0}
                        max={100}
                        value={rowProgress}
                        onChange={(event) => handleProgressChange(project._id, Number(event.target.value))}
                        onMouseUp={() => commitProgress(project._id)}
                        onTouchEnd={() => commitProgress(project._id)}
                        className="mt-2 w-full accent-blue-500"
                      />
                      {busy && <p className="text-xs text-blue-500">Updating…</p>}
                    </td>
                    <td>
                      <p className="text-sm font-medium text-slate-900">
                        {project.taskSummary?.completed || 0}/{project.taskSummary?.total || 0}
                      </p>
                      <p className="text-xs text-slate-500">tasks complete</p>
                    </td>
                    <td>
                      <p className="text-sm text-slate-900">{formatDate(project.dueDate)}</p>
                    </td>
                    <td>
                      <button
                        type="button"
                        onClick={(event) => {
                          event.stopPropagation();
                          onDelete(project._id);
                        }}
                        className="rounded-full border border-slate-200 p-2 text-slate-400 hover:border-rose-200 hover:text-rose-500"
                      >
                        <Trash2 size={14} />
                      </button>
                    </td>
                  </tr>
                );
              })
            ) : (
              <tr>
                <td colSpan={6} className="py-10 text-center text-slate-500">
                  No projects yet. Use the button above to add your first project.
                </td>
              </tr>
            )}
          </tbody>
        </table>
      </div>

      {showForm && (
        <form
          id="project-create"
          onSubmit={handleSubmit}
          className="grid gap-4 rounded-2xl border border-slate-200 bg-slate-50/60 p-5 md:grid-cols-2"
        >
          <label className="flex flex-col gap-1 text-sm text-slate-600">
            Project name
            <input
              required
              placeholder="Seaside Villa"
              value={form.name}
              onChange={(event) => setForm((prev) => ({ ...prev, name: event.target.value }))}
            />
          </label>
          <label className="flex flex-col gap-1 text-sm text-slate-600">
            Studio
            <input
              placeholder="Core Studio"
              value={form.studio}
              onChange={(event) => setForm((prev) => ({ ...prev, studio: event.target.value }))}
            />
          </label>
          <label className="flex flex-col gap-1 text-sm text-slate-600">
            Due date
            <input
              type="date"
              value={form.dueDate}
              onChange={(event) => setForm((prev) => ({ ...prev, dueDate: event.target.value }))}
            />
          </label>
          <label className="flex flex-col gap-1 text-sm text-slate-600">
            Accent color
            <input
              type="color"
              className="h-11 cursor-pointer rounded-xl border border-slate-200"
              value={form.color}
              onChange={(event) => setForm((prev) => ({ ...prev, color: event.target.value }))}
            />
          </label>
          <div className="md:col-span-2 flex justify-end">
            <button type="submit" disabled={submitting} className="btn-primary">
              <Plus size={16} />
              {submitting ? 'Creating…' : 'Create project'}
            </button>
          </div>
        </form>
      )}
    </section>
  );
}
